import React, { Component } from 'react';
import Img from '../Img';
import navlogo from '../../assets/img/nav-logo.png';

import { Navbar, Nav, NavItem } from 'react-bootstrap';
export default class Navi extends Component {
  render() {
    return (
      <Navbar inverse>
        <Navbar.Header>
          <Navbar.Brand>
            <a href="#home">
              <Img src={navlogo} alt="Welcome" />
            </a>
          </Navbar.Brand>
          <Navbar.Toggle />
        </Navbar.Header>
        <Navbar.Collapse>
          <Nav pullRight>
            <NavItem eventKey={1} href="#services">SERVICES</NavItem>
            <NavItem eventKey={2} href="#clients">CLIENTS</NavItem>
            <NavItem eventKey={3} href="#portfolio">PORTFOLIO</NavItem>
            <NavItem eventKey={4} href="#team">TEAM</NavItem>
            <NavItem eventKey={5} href="#contact">CONTACT</NavItem>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    )
  }
}



